@import "sass/index";

.root {
  position: relative;
  margin: 30px 0;
  padding: 25px;

  @media (max-width: 400px) {
    padding: 15px;
  }
}
.overlay {
  position: absolute;
  background: use(--template-box-background);
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0.1;
  border-radius: 8px;
}

.title {
  color: use(--template-header-color);
  font-size: 2.0625rem;
  letter-spacing: -2px;
  font-weight: bold;
  font-family: "Gilroy";
}
.valueBox {
  max-width: 550px;
}

