@import "sass/index";

.wrapper {
  top: 0;
  left: 0;
  width: 100%;
  padding: 0;
  background: use(--template-anyContent-background);

  border-radius: 10px;
  overflow: hidden;
}
.box {
  padding: 30px;

  @media (max-width: 400px) {
    padding: 15px;
  }
}
.pendingBox {
  max-width: 630px;
}
