@import "sass/index";

.root {
  background: use(--template-header-background);
  padding: 40px;

  @media (max-width: 400px) {
    padding: 20px;
  }
}
.textWrapper {
  margin-top: 30px;
}
.title {
  font-size: 2rem;
  letter-spacing: -2px;
  font-family: "Gilroy";
  color: use(--template-header-color);
  font-weight: bold;
  margin: 0;
}
.text {
  color: use(--template-header-color);
  opacity: 0.7;
  font-size: 1.375rem;
  letter-spacing: -1px;
  line-height: 1.875rem;
  font-weight: 400;
  font-family: "Varela Round", sans-serif;
  margin: 0;
}

.imgWrapper {
}
.img {
  width: 100%;
  max-height: 45px;
  max-width: 45px;
}
