@import "sass/index";

.root {
  display: flex;
  align-items: center;

  @media (max-width: 500px) {
    flex-direction: column;
  }
}
.contentBox {
  position: relative;
  width: max-content;
  margin-right: 15px;

  @media (max-width: 500px) {
    margin-bottom: 15px;
    margin-right: 0;
  }
}
.overlay {
  position: absolute;
  background: use(--template-box-background);
  height: 100%;
  width: 100%;
  opacity: 0.1;
  border-radius: 8px;
}
.textWrapper {
  margin: 10px;
  font-family: "Gilroy";
  color: use(--template-box-color);
  width: max-content;
}
.title {
  font-size: 0.75rem;
  text-transform: uppercase;
  letter-spacing: 1px;
  opacity: 0.5;
  font-weight: bold;
  margin: 0;
}
.value {
  font-size: 1.4375rem;
  font-weight: bold;
  margin: 0;
  max-width: 230px;
  overflow: hidden;
  text-overflow: ellipsis;
  line-height: 100%;
}
.coin {
  font-size: 0.75rem;
  letter-spacing: 1px;
  opacity: 0.4;
  margin: 0;
}

.dataText {
  font-size: 0.75rem;
  font-family: "Varela Round", sans-serif;
  font-weight: 400;
  color: use(--template-box-color);
  line-height: 1.0625;
  opacity: 0.5;
  margin: 0;
  width: fit-content;
}
