@import "sass/index";

.root {
}
.tableWrapper {
}

.th {
  border: none;
  padding: 10px;
  text-transform: uppercase;
  text-align: left;

  font-size: 0.625rem;
  letter-spacing: 1;
  color: use(--template-statisticsBlock-tab-tableHead);
  font-weight: bold;
  font-family: "Gilroy";

  &:first-child {
    padding-left: 0;
  }
}

.td {
  border-bottom: 5px solid transparent;
  padding: 0;

  &:first-child {
    & > div {
      border-top-left-radius: 4px;
      border-bottom-left-radius: 4px;
    }
  }
  &:last-child {
    & > div {
      border-top-right-radius: 4px;
      border-bottom-right-radius: 4px;
    }
  }
}

.cellWrapper {
  background: use(--template-box-background, 0.1);
  padding: 10px;
  letter-spacing: 1px;
  text-align: left;
  color: use(--template-box-color);
  opacity: 0.6;
  font-family: "Gilroy";
  font-size: 0.75rem;
  font-weight: 500;
  white-space: nowrap;
}
